function _filtered_MH_data_array(mental_health_data_object)
{
  function checkDay(date_object) {
    let day_of_the_week = date_object.date.getDay();
    let difference_from_day = 6 - day_of_the_week;
    date_object.date.setDate(date_object.date.getDate() + difference_from_day);
  }

  for (let i = 0; i<mental_health_data_object.length; i++) {
    checkDay(mental_health_data_object[i])
  }
  return mental_health_data_object;
}


