function _21(vl,total_interpolated,year_range_def)
{

  const positive_line_graph = vl.markLine()
    .encode(
      vl.color().value('orange')
    ).width(500)

  const positive_circle = vl.markCircle()
    .encode(
      vl.tooltip(['Number of scores collected', 'mean_positive_score', 'week_period']),
      vl.color().value('black'),
      vl.size().value('50')
    )
    const negative_line_graph = vl.markLine()
    .encode(
      vl.color().value('red')
    ).width(500)

  const negative_circle = vl.markCircle()
    .encode(
      vl.tooltip(['Number of scores collected', 'mean_negative_score', 'week_period']),
      vl.color().value('black'),
      vl.size().value('50'),
    )

  const depression_line_graph = vl.markLine()
    .encode(
      vl.color().value('green')
    ).width(500)

  const depression_circle = vl.markCircle()
    .encode(
      vl.tooltip(['Number of scores collected', 'depression_score', 'week_period']),
      vl.color().value('black'),
      vl.size().value('50')
    )

  const anxiety_line_graph = vl.markLine()
    .encode(
      vl.color().value('blue')
    ).width(500)

  const anxiety_circle = vl.markCircle()
    .encode(
      vl.tooltip(['Number of scores collected', 'anxiety_score', 'week_period']),
      vl.color().value('black'),
      vl.size().value('50')
    )
  const positive_layer = vl.layer(positive_line_graph, positive_circle)
    .title("Mean Positive Score Value over Quarter Weeks")
    .encode(
      vl.y().fieldQ('mean_positive_score').scale({domain: [0, 10]}).title('Mean Attribute Value'),
      vl.x().fieldO('week').axis({labelAngle: 0})
    )
  const negative_layer = vl.layer(negative_line_graph, negative_circle)
    .title("Mean Negative Score Value over Quarter Weeks")
    .encode(
      vl.y().fieldQ('mean_negative_score').scale({domain: [0, 10]}).title('Mean Attribute Value'),
      vl.x().fieldO('week').axis({labelAngle: 0})
    )
  const depression_layer = vl.layer(depression_line_graph, depression_circle)
    .title("Mean PHQ4 Depression Value over Quarter Weeks")
    .encode(
      vl.y().fieldQ('depression_score').scale({domain: [0, 3]}).title('Mean Depression Value'),
      vl.x().fieldO('week').axis({labelAngle: 0})
    )
  const anxiety_layer = vl.layer(anxiety_line_graph, anxiety_circle)
    .title("Mean PHQ4 Anxiety Value over Quarter Weeks")
  .encode(
      vl.y().fieldQ('anxiety_score').scale({domain: [0, 3]}).title('Mean Anxiety Value'),
      vl.x().fieldO('week').axis({labelAngle: 0})
    )

  const graph_1 = vl.hconcat(positive_layer, negative_layer);
  const graph_2 = vl.hconcat(anxiety_layer, depression_layer);

  const layer_object = vl.vconcat(graph_1, graph_2)
    .data(total_interpolated)
    .transform(
      vl.filter(`isNaN(datum.phq4_) == false && datum.Year == ${year_range_def}`),
      vl.groupby(['week','week_period'])
      .aggregate(
        vl.average('negative_affect_EMA').as('mean_negative_score'),
        vl.average('positive_affect_EMA').as('mean_positive_score'),
        vl.average('phq4_depression_EMA').as('depression_score'),
        vl.average('phq4_anxiety_EMA').as('anxiety_score'),
        vl.count().as('Number of scores collected')
      ),
    )
    .render()

  return layer_object;
//positive effect
//steps evening and steps night
}


