function _final_chart(year_steps,total_interpolated,$0,vl,participant_dropdown,width,Event)
{
  let filtered_year;
  if (year_steps === 2021) {
    filtered_year = total_interpolated.filter(x=>x.Year == 2021);
  } else if (year_steps === 2020) {
    filtered_year = total_interpolated.filter(x=>x.Year == 2020);
  } else {
    filtered_year = total_interpolated.filter(x=>x.Year == 2019);
  }
  //f_slp:fitbit_sleep_intraday_rapids_sumdurationasleepunifiedmain:allday
  const student_val = $0;

  const selection2 = vl.selectPoint('select')
    .init(1)
    .fields('week')
    .nearest(true)
   
  const circle_graph = vl.markPoint()
  .title('Effect of Total Steps on PANAS Positive Mental Health Score')
  .transform(
     vl.filter(selection2.empty(false))
  )
  .encode(
    vl.x().fieldQ('f_steps:fitbit_steps_intraday_rapids_sumsteps:allday').title("Average steps in week"),
    vl.y().fieldQ('positive_affect_EMA').title("PANAS Positive Score"),
    vl.color().condition({test:`'${participant_dropdown}' == 'All' ? true : datum.pid ==='${participant_dropdown}'`,value:'#DB0000'}).value("gray"),
    vl.opacity().condition({test:`'${participant_dropdown}' == 'All' ? true : datum.pid ==='${participant_dropdown}'`,value:'1'}).value(0.3),
    vl.size().value(80),
    vl.tooltip(['pid', {'field': 'f_steps:fitbit_steps_intraday_rapids_sumsteps:allday', 'format': "0.2f", 'title': "Average Steps"}, 'positive_affect_EMA', {'field': 'f_loc:phone_locations_doryab_radiusgyration:allday', 'format': "0.2f", 'title': "Area covered"}])
  )

  const trend_line = vl.markLine({strokeDash: [8]})
    .transform(
       vl.filter(selection2.empty(false)),
        vl.regression('f_steps:fitbit_steps_intraday_rapids_sumsteps:allday').on('positive_affect_EMA')
    ).encode(
      vl.x().fieldQ('f_steps:fitbit_steps_intraday_rapids_sumsteps:allday'),
      vl.y().fieldQ('positive_affect_EMA'),
      
      vl.color().value('black')
    )

  const layer_object = vl.layer(circle_graph, trend_line)
    .config({"background": null, transition: 500})
    .width(Math.max(.3 * width, 500))
    .height(500)
   // .toSpec()
   
   const slider_line = vl.markLine()
    .encode(
      vl.color().value('grey')
    )

   const slider_circle = vl.markCircle()
    .params(selection2)
    .encode(
      vl.size().value('50'),
      vl.color().if(selection2.empty(false), vl.value('red')).value('grey'),
    )


   const slider_layer = vl.layer(slider_line, slider_circle)
    .width(Math.max(.3 * width, 500))
    .height(100)
    .title('Student Weekly Average Steps Over Time')
    .transform(
      vl.filter(`'${participant_dropdown}' == 'All' ? true : datum.pid ==='${participant_dropdown}'`),
      vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:allday').as('mean_average_steps')
    )
    .encode(
      vl.x().fieldQ('week'),
      vl.y().fieldQ('f_steps:fitbit_steps_intraday_rapids_sumsteps:allday').title('Average steps in the week')
    ).config({"background": null})


   return vl.vconcat(slider_layer, layer_object)
     .data(filtered_year)
     .config({"background": null})
     .autosize({type: 'fit-x', contains: 'padding'})
     .render()
     .then((chart) => {
          setTimeout(() => window.dispatchEvent(new Event("resize")), 1);
          return chart;
        })

}


