function _steps_time_breakdown(steps_breakdown,total_interpolated,vl,width,innerWidth)
{

  let filtered_year;
  if (steps_breakdown === 2021) {
    filtered_year = total_interpolated.filter(x=>x.Year == 2021);
  } else if (steps_breakdown === 2020) {
    filtered_year = total_interpolated.filter(x=>x.Year == 2020);
  } else {
    filtered_year = total_interpolated.filter(x=>x.Year == 2019);
  }
  // Aggregating for each student over each week

  const brush = vl.selectInterval()
//    .encodings('y');

  const markPoint = vl.markPoint()
    .title('student phone usage all day over 12 week quarter')
    .params(brush)
    .encode(
       vl.x().fieldO('week_period'),
       vl.y().fieldQ('f_steps:fitbit_steps_intraday_rapids_sumsteps:allday').title('student steps'),
    //  vl.color().value('green')
    ).height(150).width(0.6 * width)
  
  const morning_circle_graph = vl.markCircle()
    .title('Morning Phone Usage')
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
      .aggregate(
        vl.average('positive_affect_EMA').as('panas'),
        vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:morning').as('average_morn_steps')
      )
    ).encode(
      vl.x().fieldQ('average_morn_steps').title('average number of steps'),
      vl.y().fieldQ('panas').scale({domain: [0, 20]}).title('PANAS Positive Score'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Morning Steps', field:'average_morn_steps'}, {title: 'Average PANAS Score', field:'panas'}]),
    //  vl.color().value('#FFFF00'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    );

  const morning_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('positive_affect_EMA').as('panas'),
            vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:morning').as('average_morn_steps')
        ),
      vl.regression('panas').on('average_morn_steps')
    ).encode(
      vl.y().fieldQ('panas').scale({domain: [0, 20]}),
      vl.x().fieldQ('average_morn_steps'),
      vl.color().value('red')
    )

  const morning_layer = vl.layer(morning_circle_graph, morning_trend_line).width(width/4).height(200);



  const afternoon_circle_graph = vl.markCircle()
    .title({text: 'Afternoon Phone Usage'})
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
      .aggregate(
        vl.average('positive_affect_EMA').as('panas'),
        vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:afternoon').as('average_aft_steps')
      )
    ).encode(
      vl.x().fieldQ('average_aft_steps').title('average number of steps'),
      vl.y().fieldQ('panas').scale({domain: [0, 20]}).title('PANAS Positive Score'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Afternoon Steps', field:'average_aft_steps'}, {title: 'Average PANAS Score', field:'average_sleep_time'}]),
     // vl.color().value('#86B7E0'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    );

  const afternoon_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('positive_affect_EMA').as('panas'),
            vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:afternoon').as('average_aft_steps')
        ),
      vl.regression('panas').on('average_aft_steps')
    ).encode(
      vl.y().fieldQ('panas').scale({domain: [0, 20]}),
      vl.x().fieldQ('average_aft_steps'),
      vl.color().value('red')
    )

  const afternoon_layer = vl.layer(afternoon_circle_graph, afternoon_trend_line).width(width/4).height(200);



  const evening_circle_graph = vl.markCircle()
    .title('Evening Phone Usage')
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
      .aggregate(
        vl.average('positive_affect_EMA').as('panas'),
        vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:evening').as('average_even_steps')
      )
    ).encode(
      vl.x().fieldQ('average_even_steps').title('average number of steps'),
      vl.y().fieldQ('panas').scale({domain: [0, 20]}).title('PANAS Positive Score'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Evening Steps', field:'average_even_steps'}, {title: 'Average PANAS Score', field:'panas'}]),
      // vl.color().value('#BF77F6'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    );

  const evening_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('positive_affect_EMA').as('panas'),
            vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:evening').as('average_even_steps')
        ),
      vl.regression('panas').on('average_even_steps')
    ).encode(
      vl.y().fieldQ('panas').scale({domain: [0, 20]}),
      vl.x().fieldQ('average_even_steps'),
      vl.color().value('red')
    );

  const evening_layer = vl.layer(evening_circle_graph, evening_trend_line).width(width/4).height(200);


   const night_circle_graph = vl.markCircle()
    .title({text: 'Night Phone Usage', fontSize: "12"})
    .transform(
      vl.filter(brush),
        // vl.filter('datum.pid == student_selector || student_selector == "ALL"'),
      vl.groupby('pid')
      .aggregate(
        vl.average('positive_affect_EMA').as('panas'),
        vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:night').as('average_night_steps')
      )
    ).encode(
      vl.x().fieldQ('average_night_steps').title('average number of steps'),
      vl.y().fieldQ('panas').scale({domain: [0, 20]}).title('PANAS Positive Score'),
      vl.tooltip([{title: 'Student Identifier', field:'pid'}, {title: 'Average Night Steps', field:'average_night_steps'}, {title: 'Average PANAS Score', field:'panas'}]),
    //  vl.color().value('#999999'),
      vl.stroke().value('#000000'),
      vl.strokeWidth().value(.2)
    )

  const night_trend_line = vl.markLine()
    .transform(
      vl.filter(brush),
      vl.groupby('pid')
         .aggregate(
            vl.average('positive_affect_EMA').as('panas'),
            vl.average('f_steps:fitbit_steps_intraday_rapids_sumsteps:night').as('average_night_steps')
        ),
      vl.regression('panas').on('average_night_steps')
    ).encode(
      vl.y().fieldQ('panas').scale({domain: [0, 20]}),
      vl.x().fieldQ('average_night_steps'),
      vl.color().value('red')
    );

  const night_layer = vl.layer(night_circle_graph, night_trend_line).width(width/4).height(200);


  const main_object = vl.vconcat(vl.hconcat(morning_layer, afternoon_layer).spacing(innerWidth*.06), vl.hconcat(evening_layer, night_layer).spacing(innerWidth*.06)).config({dy: "10"})
    .title({text: 'Impact of student screen time on average night sleep scores', fontSize: "15", anchor: "start", dy: ".1"});

  

  return vl.vconcat(markPoint, main_object).config({background: null}).spacing(30)
    .data(filtered_year).render();  
}


